.Home .lander {
    padding: 80px 0;
    text-align: center;
  }
  
  .Home .lander h1 {
    
    font-weight: 600;
  }
  .App-logo {
    height: 40vmin;
    pointer-events: none;
    
  }
@media all and (min-width: 480px) {
  .Login {
    padding: 60px 0;
  }

  .Login form {
    margin: 0 auto;
    max-width: 320px;
  }
}
.NotFound {
    padding-top: 100px;
  }
